@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.add-form {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 360px;
  height: 500px;
  margin: auto;
  background: black;
  opacity: 0.9;
  border-radius: 5px;
  color: white;
  padding: 10px;
  box-sizing: border-box;
}

.add-form > div {
  min-height: 30px;
}

.close-button {
  text-align: right;
}

.add-form-input {
  display: grid;
  grid-template-columns: 120px 160px;
  color: white;
  justify-content: center;
  width: 100%;
  text-align: left;
  align-content: space-evenly;
}

.add-form-input > div {
  height: 50px;
}

.add-form-input input, select {
  color: white;
  width: 140px;
}

.add-form #add {
  background: #F7CE3E;
  width: 100%;
  height: 45px;
  border-radius: 3px;
  opacity: 0.9;
  box-shadow: 2px 2px rgba(0, 0, 0, 0.3);
}

.one-column1 {
 grid-column: 1 / span 2;
}

.one-column1 > div {
