.button-reg {
    display: inline-block;
    border-radius: 6px;
    background-color: rgb(144, 40, 165);
    border: none;
    color: rgb(224, 198, 226);
    text-align: center;
    font-size: 20px;
    padding: 3px;
    width: 130px;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
    cursor: pointer;
    margin: 10px auto;
    text-align: center;
    box-shadow: 3px 3px 3px 1px rgba(7, 7, 7, 0.5);

  }
  
  .button-reg span {
    cursor: pointer;
    display: inline-block;
    position: relative;
    -webkit-transition: 0.5s;
    transition: 0.5s;
  }
  
  .button-reg span:after {
    content: '\BB';
    position: absolute;
    opacity: 0;
    top: 0;
    right: -20px;
    -webkit-transition: 0.5s;
    transition: 0.5s;
  }
  .button-reg:active{
    background-color: rgb(109, 32, 124);
  }
  .button-reg:hover span {
    padding-right: 25px;
  }
  
  .button-reg:hover span:after {
    opacity: 1;
    right: 0;
  }
